


.form_title {
	color: #646cff;
	margin: 15px 7px 5px;
	font-weight: 600;
	font-size: 22px;
	line-height: 23px;
}

.cmp_dtls{
	padding: 15px 3px;
}


.cmp_desc{
	margin-top: 25px;
	padding: 15px 3px;
	border-top: 1px solid #dadce0;
	text-align: left;
}

.cmp_desc .description{
	margin-left: 12px;
}


.desc_lbl{
	margin-right: 8px;
}

.hdr_bar{
	padding: 10px 7px;
	border-radius: 8px;
}







.boxAddAcc{
	padding: 16px;
    border: 1px solid var(--color_text_dad);
    border-radius: 8px;
}



.icon svg{
	cursor: pointer;
}












.acc_dtls{
	padding: 0 11px 11px;
	margin-left: 30px;
}

.acc_dtls .code{
	margin-right: 12px;
	font-weight: 600;
	font-size: 18px;
	line-height: 18px;
}
.acc_dtls .name{
	font-weight: 500;
	line-height: 18px;
}



.comprobante_id{
	color: #646cff;
	font-weight: 600;
	cursor: pointer;
}



.tr_bar_s6{
	flex-basis: 16.66666%;
	width: 16.66666%;
}

.tr_bar_20{
	flex-basis: 20%;
	width: 20%;
}



.subMHeight{
	height:  calc(70vh - 100px)
}

.subMheader{
	font-family: "Google Sans","GoogleSans-Local";
}



.employee_name{
	font-weight: var(--bold);
    color: var(--hrm_palette-imput-label_gray);
	font-family: "Google Sans","GoogleSans-Local";
}

.icon_revision{
	margin-right: 14px;
}